.outer-container {
  position: relative;
  overflow: hidden;
}
.outer-container .community_table::-webkit-scrollbar {
  display: none;
}
.community_table {
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  white-space: nowrap;
  margin: 1px 0px -1px 0;
  padding: 0px;
  color: white;
  border: 1px solid #00ffeb;
  background-color: rgba(53, 150, 218, 0.1);
  border-radius: 2px;
}
.community_table .mainbox {
  width: 100%;
}
.community_table .responsive {
  margin: 0;
}
.community_table .mainbox table {
  border-collapse: collapse;
  border-top: 0 !important;
  width: 100%;
}
.community_table .mainbox table th {
  color: #fff;
  height: 75px;
  min-width: 80px;
  max-width: 60px;
  white-space: nowrap;
  border: none;
  border-radius: 10px 10px 0px 0px;
}
.community_table .mainbox table th,
.community_table .mainbox table td {
  text-align: center;
  font-size: 16px;
  line-height: 30px;
  background: rgba(57, 135, 137, 0);
}
.community_table .mainbox tr:nth-child(2n+1) td {
  background: rgba(11, 128, 165, 0.31);
}
.community_table .mainbox table td {
  border-bottom: 1px #00f1e6 solid;
}
