.portrait .layout {
  background: url(/static/media/page_bg.41fd1031.jpg) no-repeat;
  background-size: 100% 100%;
}
.portrait .logo {
  height: 60%;
  margin-top: -20px;
}
.portrait .title {
  color: white;
  font-size: 24px;
  margin-top: -5px;
  padding-left: 5px;
}
.portrait .ant-layout-header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.portrait .ant-layout-header .title-img {
  width: 100%;
}
.portrait .col-margin {
  padding-bottom: 15px;
}
.portrait .home-link {
  position: absolute;
  top: 0;
  left: 0;
  background: url(data:image/png;base64,);
  background-size: cover;
  width: 50px;
  height: 50px;
  cursor: pointer;
}
.portrait .home-link:hover {
  background: url(data:image/png;base64,);
  background-size: cover;
}
