.community_popup {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
@font-face {
  font-family: yjsz;
  src: url(/static/media/yjsz.048ff21f.TTF);
}
.community_popup .popup_box {
  background: url(/static/media/popup_bg.45787d74.png) no-repeat;
  background-size: contain;
  position: fixed;
  z-index: 999;
  top: -20vh;
  left: 0;
  right: 0;
  bottom: 0;
  width: 600px;
  height: 340px;
  margin: auto;
  padding: 45px 40px 45px 60px;
}
.community_popup .popup_body {
  width: 100%;
  height: 100%;
  display: flex;
}
.community_popup .close_popup {
  width: 60px;
  height: 24px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  cursor: pointer;
  position: absolute;
  z-index: 999;
  top: 5px;
  right: 20px;
}
.community_popup .close_popup:hover {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.community_popup .popup_body_left,
.community_popup .popup_body_right {
  width: 250px;
  height: 250px;
  padding: 10px;
}
.community_popup .body_left_villageImg {
  width: 230px;
  height: 180px;
  border: 2px solid #32ebff;
  border-radius: 10px;
  background-color: white;
}
.community_popup .villageImg_style {
  width: 226px;
  height: 176px;
  border-radius: 8px;
}
.community_popup .villageImg_bottom_style {
  width: 230px;
  height: 50px;
  padding-top: 15px ;
  display: flex;
  text-align: center;
}
.community_popup .villageImg_bottom_left,
.community_popup .villageImg_bottom_right {
  width: 230px;
  height: 40px;
  margin-bottom: 20px;
  text-align: center;
  color: #0dffee;
  line-height: 40px;
  font-size: 22px;
  font-weight: 500;
  cursor: pointer;
  margin-left: 0px;
}
.community_popup .villageImg_bottom_left:hover,
.community_popup .villageImg_bottom_right:hover {
  background: url(/static/media/button_bg_hover.dd7b93e7.png) no-repeat;
  background-size: 100% 100%;
  color: #3fff9a;
}
.community_popup .villageImg_bottom_left {
  background: url(/static/media/button_bg.3ac5fa3b.png) no-repeat;
  background-size: 100% 100%;
}
.community_popup .villageImg_bottom_right {
  background: url(/static/media/button_bg.3ac5fa3b.png) no-repeat;
  background-size: contain;
}
.community_popup .body_right_title {
  color: white;
  border-bottom: 3px solid #32ebff;
  height: 35px;
  margin-bottom: 20px;
}
.community_popup .body_right_text {
  color: #17fffc;
  font-size: 18px;
  height: 40px;
  padding: 0 0 0 20px;
  margin: 0;
}
.community_popup .body_right_number {
  font-family: yjsz;
  font-size: 20px;
  font-weight: 600;
  color: #ffe400;
  letter-spacing: 5px;
}
