@font-face {
  font-family: yjsz;
  src: url(/static/media/yjsz.048ff21f.TTF);
}
.information-card .visual {
  height: calc(100% - 33px);
  padding-top: 10px;
  background-color: #00d8ff;
}
.information-card .visual_box {
  height: 33.3%;
}
.information-card .visual_box .visual_title {
  position: relative;
  height: 35px;
  margin: 5px 0;
}
.information-card .visual_box .visual_title span {
  color: #fff;
  font-size: 18px;
  line-height: 35px;
}
.information-card .visual_box .visual_title img {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}
.information-card .visual_box .visual_chart {
  height: calc(100% - 35px);
}
.information-card .visual_left {
  width: 25%;
  height: 100%;
  float: left;
}
.information-card .visual_left .sfzcll {
  position: relative;
}
.information-card .visual_left .sfzcll a {
  width: 32%;
  text-align: center;
  line-height: 25px;
  color: #fff;
  display: inline-block;
}
.information-card .visual_left .sfzcll .sfzcll_pos_box {
  width: 100%;
  height: calc(100% - 30px);
}
.information-card .visual_left .sfzcll .sfzcll_box {
  width: 100%;
  height: calc(33% - 15px);
  line-height: 5;
  background: rgba(15, 47, 72, 0.8);
  border: 1px solid #1380bd;
  position: relative;
  margin-bottom: 15px;
}
.information-card .visual_left .sfzcll .sfzcll_box:last-of-type {
  margin: 0;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_bkJk {
  position: absolute;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_bkJk:first-of-type {
  top: -2px;
  left: -2px;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_bkJk:nth-child(2) {
  top: -2px;
  right: -2px;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_bkJk:nth-child(3) {
  bottom: -2px;
  right: -2px;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_bkJk:nth-child(4) {
  bottom: -2px;
  left: -2px;
}
.information-card .visual_left .sfzcll .sfzcll_box label {
  width: 33%;
  color: #fff;
  text-align: center;
  float: left;
}
.information-card .visual_left .sfzcll .sfzcll_box label img {
  width: 23px;
  height: 23px;
  margin-right: 5px;
  margin-top: -2px;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_smallBk {
  width: 33%;
  height: 100%;
  float: left;
  padding: 2px 5px;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_smallBk > div {
  height: 100%;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAJkAAAAgAQMAAAA7V0hEAAAABGdBTUEAALGPC/xhBQAAAAFzUkdCAK7OHOkAAAAGUExURRA2Vwem/w4aIw4AAAAfSURBVCjPY/jBgA74GxgaMAQZSBEcBSMTUJhssCVFAO2OCQ+KHsmQAAAAAElFTkSuQmCC) no-repeat;
  background-size: 100% 100%;
  text-align: center;
  line-height: 1.5em;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_smallBk .ygl {
  color: #00ffc6;
}
.information-card .visual_left .sfzcll .sfzcll_box .sfzcll_smallBk .ygh {
  color: #ffe400;
}
.information-card .visual_con {
  width: 100%;
  height: 100%;
  float: left;
  padding: 3vh 2% 0 2% ;
}
.information-card .visual_con .visual_conTop {
  height: 15vh;
  margin-bottom: 1vh;
}
.information-card .visual_con .visual_conTop .visual_conTop_box {
  height: 100%;
  padding: 0 3px;
}
.information-card .visual_con .visual_conTop .visual_conTop1 {
  width: 20%;
  height: 100%;
  float: left;
}
.information-card .visual_con .visual_conTop .visual_conTop1 > div {
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100% 100%;
}
.information-card .visual_con .visual_conTop .visual_conTop2 {
  width: 30%;
  height: 100%;
  float: left;
}
.information-card .visual_con .visual_conTop .visual_conTop2 > div {
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100% 100%;
}
.information-card .visual_right {
  width: 25%;
  height: 100%;
  float: right;
}
.information-card .visual_con .visual_conTop .visual_conTop_box > div {
  height: 100%;
}
.information-card .visual_con .visual_conTop .visual_conTop_box > div h3 {
  color: #fff;
  font-size: 20px;
  padding: 2.2vh 0 0 10%;
}
.information-card .visual_con .visual_conTop .visual_conTop_box > div p {
  width: 33%;
  float: left;
  line-height: 30px;
  color: #20dbfd;
  text-shadow: 0 0 25px #00d8ff;
  font-size: 46px;
  font-family: 'yjsz';
  text-align: right;
  padding-left: 10%;
}
.information-card .visual_con .visual_conBot {
  height: calc(100% - 136px);
  background: url(/static/media/ksh41.1f4468bf.png) no-repeat;
  background-size: 100% 100%;
  position: relative;
}
.information-card .visual_con .visual_conBot > img {
  position: absolute;
  width: 25px;
  height: 25px;
}
.information-card .visual_con .visual_conBot .visual_conBot_l {
  position: absolute;
  left: 0;
  top: 0;
}
.information-card .visual_con .visual_conBot .visual_conBot_2 {
  position: absolute;
  right: 0;
  top: 0;
}
.information-card .visual_con .visual_conBot .visual_conBot_3 {
  position: absolute;
  right: 0;
  bottom: 0;
}
.information-card .visual_con .visual_conBot .visual_conBot_4 {
  position: absolute;
  left: 0;
  bottom: 0;
}
.information-card .visual_con .visual_conBot .visual_chart {
  width: 100%;
  height: 100%;
  position: absolute;
}
.information-card .visual_con .visual_conBot .visual_chart_text {
  color: #fff;
  position: absolute;
  top: 15px;
  left: 15px;
  z-index: 99;
}
.information-card .visual_con .visual_conBot .visual_chart_text h1 {
  font-size: 26px;
  margin-bottom: 6px;
}
.information-card .visual_con .visual_conBot .visual_chart_text h2 {
  font-size: 20px;
}
.information-card .visual_con .visual_conBot .visual_conBot_bot {
  width: calc(100% - 8px);
  height: 180px;
  background: rgba(16, 54, 87, 0.5);
  border: 1px solid #345f92;
  position: absolute;
  bottom: 4px;
  left: 4px;
  z-index: 99;
}
