.house_info_popup {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
.house_info_popup .popup_box {
  background: url(/static/media/popup_bg.45787d74.png) no-repeat;
  background-size: contain;
  position: fixed;
  z-index: 999;
  top: -20vh;
  left: 0;
  right: 0;
  bottom: 0;
  width: 600px;
  height: 340px;
  margin: auto;
  padding: 45px 40px 45px 60px;
  overflow: hidden;
}
.house_info_popup .popup_body {
  width: 100%;
  height: 100%;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
  white-space: nowrap;
}
.house_info_popup .popup_body::-webkit-scrollbar {
  display: none;
}
.house_info_popup .outer-container {
  position: relative;
  overflow: hidden;
}
.house_info_popup .close_popup {
  width: 60px;
  height: 24px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  cursor: pointer;
  position: absolute;
  z-index: 999;
  top: 5px;
  right: 20px;
}
.house_info_popup .close_popup:hover {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.house_info_popup .popup_body_left,
.house_info_popup .popup_body_right {
  width: 250px;
  height: 250px;
  padding: 10px;
}
.house_info_popup .mainbox {
  width: 100%;
}
.house_info_popup .responsive {
  margin: 0;
}
.house_info_popup .mainbox table {
  border-collapse: collapse;
  border-top: 0 !important;
  width: 100%;
  color: white;
}
.house_info_popup .mainbox table th {
  color: #fff;
  height: 75px;
  min-width: 80px;
  max-width: 60px;
  white-space: nowrap;
  border: none;
  border-radius: 10px 10px 0px 0px;
}
.house_info_popup .mainbox table th,
.house_info_popup .mainbox table td {
  text-align: center;
  font-size: 16px;
  line-height: 30px;
  background: rgba(1, 62, 68, 0.87);
}
.house_info_popup .mainbox tr:nth-child(2n+1) td {
  background: #064b61;
}
.house_info_popup .mainbox table td {
  border-bottom: 1px #00f1e6 solid;
}
